export default "import React, { ReactElement } from 'react';\r\nimport { Link } from 'react-router-dom';\r\n\r\nimport List from '@material-ui/core/List';\r\nimport ListItem from '@material-ui/core/ListItem';\r\nimport ListItemIcon from '@material-ui/core/ListItemIcon';\r\nimport ListItemText from '@material-ui/core/ListItemText';\r\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\r\nimport Switch from '@material-ui/core/Switch';\r\n\r\nconst NavBar = (props) => {\r\n  const handleChange = () => {\r\n    props.toggleCodeView();\r\n  };\r\n\r\n  const populatePages = () => {\r\n    const populatedPages: ReactElement[] = [];\r\n    for (const page of props.pages) {\r\n      populatedPages.push(\r\n        <Link\r\n          to={page.route}\r\n          onClick={() => props.navClick(page)}\r\n          key={page.text}\r\n        >\r\n          <ListItem button>\r\n            <ListItemIcon>\r\n              <i className=\"material-icons\">{page.icon}</i>\r\n            </ListItemIcon>\r\n            <ListItemText primary={page.text} />\r\n          </ListItem>\r\n        </Link>\r\n      );\r\n    }\r\n    return populatedPages;\r\n  };\r\n\r\n  const populateOptions = () => {\r\n    const options: ReactElement[] = [\r\n      <ListItem className=\"code-view-bar\">\r\n        <FormControlLabel\r\n          control={\r\n            <Switch\r\n              checked={props.codeView}\r\n              onChange={handleChange}\r\n              name=\"codeView\"\r\n            />\r\n          }\r\n          label=\"Code View\"\r\n        />\r\n      </ListItem>,\r\n    ];\r\n    return options;\r\n  };\r\n\r\n  return (\r\n    <nav className=\"app-nav\">\r\n      <List>\r\n        {populatePages()}\r\n        {populateOptions()}\r\n      </List>\r\n    </nav>\r\n  );\r\n};\r\n\r\nexport default NavBar;\r\n";